import { Col, Image, message, Row, Table, Typography } from 'antd'
import React, { useContext, useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import { CameraOutlined, PlusCircleOutlined, CheckOutlined, CloseOutlined, EditOutlined } from '@ant-design/icons';
import { PeopleController } from '../../context/peopleContext'
import { statusCol } from './tableColumn/statusColumn'
import { testCol } from './tableColumn/testColumn'
import { relatedCol } from './tableColumn/relatedColumn'
import AddSubCase from '../case/modal/addSubCase'
import AddPeopleTest from './modal/addPeopleTest'
import AddPeopleHospital from './modal/addPeopleHospital'
import AddPeopleStatus from './modal/addPeopleStatus'
import AddPeopleQuarantine from './modal/addPeopleQuarantine'
import AddPeopleHistory from './modal/addPeopleHistory'
import UploadPic from './modal/uploadPic'
import { useMutation, useQuery } from '@apollo/client';
import { GET_PERSONALINFO_BY_ID } from '../../graphql/people';
import { GET_HOSPITAL_QUARANTINE_BY_PERSON } from '../../graphql/people';
import moment from 'moment';

const { Title } = Typography

export default function SubPeople() {
    //const { peopleData } = useContext(PeopleController)
    const { id } = useParams()

    const [personalData, setPersonalData] = useState({})
    const [openAddSubCase, setOpenAddSubCase] = useState(false)
    const [openAddPeopleTest, setOpenAddPeopleTest] = useState(false)
    const [openAddPeopleHospital, setOpenAddPeopleHospital] = useState(false)
    const [openAddPeopleStatus, setOpenAddPeopleStatus] = useState(false)
    const [openAddPeopleQuarantine, setOpenAddPeopleQuarantine] = useState(false)
    const [openAddPeopleHistory, setOpenAddPeopleHistory] = useState(false)
    const [openUploadPic, setOpenUploadPic] = useState(false)
    const [openEdit, setOpenEdit] = useState(false)

    const [quarantineData, setQuarantineData] = useState({})
    const [hospitalData, setHospitalData] = useState({})

    const { data } = useQuery(GET_PERSONALINFO_BY_ID, {
        variables: {
            id: id
        },
        onCompleted: ({ getPersonalInfoById }) => {
            console.log(getPersonalInfoById)
            setPersonalData(getPersonalInfoById)
        }
    })

    const { data: hospital_quarantine } = useQuery(GET_HOSPITAL_QUARANTINE_BY_PERSON, {
        variables: {
            persoanlInfoId: id
        },
        onCompleted: ({ getHospitalizationByPersonalInfo }) => {
            console.log(getHospitalizationByPersonalInfo)
            setQuarantineData(getHospitalizationByPersonalInfo?.quarantineInfo)
            setHospitalData(getHospitalizationByPersonalInfo?.hospitalInfo)
        }
    })

    useEffect(() => {
        //setPersonalData(peopleData[peopleData.findIndex(e => e.id === id)])
        //console.log(peopleData)
    }, [])

    const handleDelete = () => {

    }

    return (
        <Row>
            <AddSubCase open={openAddSubCase} setOpen={setOpenAddSubCase} caseId={"1"} />
            <AddPeopleTest open={openAddPeopleTest} setOpen={setOpenAddPeopleTest} peopleID={id} />
            <AddPeopleHospital open={openAddPeopleHospital} setOpen={setOpenAddPeopleHospital} />
            <AddPeopleStatus open={openAddPeopleStatus} setOpen={setOpenAddPeopleStatus} />
            <AddPeopleQuarantine open={openAddPeopleQuarantine} setOpen={setOpenAddPeopleQuarantine} />
            <AddPeopleHistory open={openAddPeopleHistory} setOpen={setOpenAddPeopleHistory} />
            <UploadPic open={openUploadPic} setOpen={setOpenUploadPic} />
            <Col
                xs={24} md={24}
                className="subPeople-card"
            >
                <Row
                    style={{
                        border: "1px solid #d9d9d9",
                        padding: "20px 20px 20px 20px",

                    }}
                >
                    <Col
                        xs={24} md={4}
                        style={{
                            position: "relative",
                            // padding: 20,
                        }}

                    >
                        <Image
                            width={"100%"}
                            src="https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png"
                        />
                        <span className="btnCamera" onClick={() => setOpenUploadPic(true)}>
                            <CameraOutlined style={{ color: '#FFFF' }} />
                        </span>
                    </Col>
                    <Col
                        xs={24} md={10}
                    >
                        <ul className="list">
                            <li> {personalData?.lastName} {personalData?.firstName} <EditOutlined className="link" onClick={() => setOpenEdit(true)} /></li>
                            <li> {personalData?.gender}</li>
                            <li> {personalData?.nationality}</li>
                            <li> {personalData?.idCard}</li>
                            <li> {personalData?.tel}</li>
                            <li> {personalData?.occupation}</li>
                            <li>  {" "}
                                {personalData?.village !== "" && "" + personalData?.village + ","}
                                {personalData?.commune !== "" && "" + personalData?.commune + ","}
                                {personalData?.district !== "" && "" + personalData?.district + ","}
                                {"" + personalData?.province}</li>
                        </ul>
                    </Col>
                    <Col
                        xs={24} md={{ span: 6}}

                    >
                        <ul className="list">
                            <li><Title level={5}> <span className="link" onClick={() => setOpenAddPeopleQuarantine(true)}><PlusCircleOutlined /></span></Title></li>
                            <li> {hospitalData?.date_in !== undefined && moment(hospitalData?.date_in).format("DD MM YYYY")}</li>
                            <li> {hospitalData?.date_out !== undefined && moment(hospitalData?.date_out).format("DD MM YYYY")}</li>
                            <li> {quarantineData?.quarantineInfo?.locationName}</li>
                            <li>  {" "}
                                {(quarantineData?.quarantineInfo?.village !== "" && quarantineData?.quarantineInfo?.village !== undefined) && "" + quarantineData?.quarantineInfo?.village + ","}
                                {(quarantineData?.quarantineInfo?.commune !== "" && quarantineData?.quarantineInfo?.commune !== undefined) && "" + quarantineData?.quarantineInfo?.commune + ","}
                                {(quarantineData?.quarantineInfo?.district !== "" && quarantineData?.quarantineInfo?.district !== undefined) && "" + quarantineData?.quarantineInfo?.district + ","}
                                {quarantineData?.quarantineInfo?.province !== undefined && quarantineData?.quarantineInfo?.province}</li>
                        </ul>
                    </Col>
                </Row>
            </Col>

            <Col
                xs={24} md={{ span: 11 }}
                style={{
                    border: "1px solid #d9d9d9",
                    padding: "20px 20px 20px 20px",
                    marginTop: 20
                }}
                className="subPeople-card"
            >
                <Title level={5}> <span className="link" onClick={() => setOpenAddPeopleStatus(true)}><PlusCircleOutlined /></span></Title>
                {/* <Table
                    columns={statusCol({ handleDelete })}
                    dataSource={[{ id: "1", date: "alsdjas", status: "laskldfj", remark: "laksdjald" }]}
                    rowKey={record => record.id}
                    pagination={true}
                    scroll={{ x: 500, y: 300 }}
                    sticky
                /> */}
                <Row>
                    <Col xs={24} md={{ span: 11 }}>
                        <ul className="list">
                            <li> {personalData?.currentState?.confirm ? <CloseOutlined style={{ color: "red" }} /> : <CheckOutlined style={{ color: "green" }} />} </li>
                            <li> {personalData?.currentState?.confirm ? <CheckOutlined style={{ color: "green" }} /> : <CloseOutlined style={{ color: "red" }} />} </li>
                            {/* <li> {personalData?.currentState?.recovered ? <CheckOutlined style={{ color: "green" }} /> : <CloseOutlined style={{ color: "red" }} />} </li>
                            <li> {personalData?.currentState?.death ? <CheckOutlined style={{ color: "green" }} /> : <CloseOutlined style={{ color: "red" }} />} </li> */}
                        </ul>
                    </Col>

                    <Col xs={24} md={{ span: 11 }}>
                        <ul className="list">
                            {/* <li> {personalData?.currentState?.confirm ? <CloseOutlined style={{ color: "red" }} /> : <CheckOutlined style={{ color: "green" }} />} </li>
                            <li> {personalData?.currentState?.confirm ? <CheckOutlined style={{ color: "green" }} /> : <CloseOutlined style={{ color: "red" }} />} </li> */}
                            <li> {personalData?.currentState?.recovered ? <CheckOutlined style={{ color: "green" }} /> : <CloseOutlined style={{ color: "red" }} />} </li>
                            <li> {personalData?.currentState?.death ? <CheckOutlined style={{ color: "green" }} /> : <CloseOutlined style={{ color: "red" }} />} </li>
                        </ul>
                    </Col>
                </Row>

            </Col>
            <Col
                xs={24} md={{ span: 12, offset: 1 }}
                style={{
                    border: "1px solid #d9d9d9",
                    padding: "20px 20px 20px 20px",
                    marginTop: 20,
                }}
                className="subPeople-card"
            >
                <Title level={5}> <span className="link" onClick={() => setOpenAddPeopleHistory(true)}><PlusCircleOutlined /></span></Title>
                <ul className="list">
                    <li> {personalData?.case?.caseName !== undefined && personalData?.case?.caseName}</li>
                    <li> {" "}
                        {(personalData?.case?.village !== "" && personalData?.case?.village !== undefined) && "" + personalData?.case?.village + ","}
                        {(personalData?.case?.commune !== "" && personalData?.case?.commune !== undefined) && "" + personalData?.case?.commune + ","}
                        {(personalData?.case?.district !== "" && personalData?.case?.district !== undefined) && "" + personalData?.case?.district + ","}
                        {personalData?.case?.province !== undefined && "" + personalData?.case?.province}
                    </li>
                    <li> {personalData?.direct ? "" : ""}</li>
                </ul>
                <Title level={5}> <span className="link" onClick={() => setOpenAddPeopleHospital(true)}><PlusCircleOutlined /></span></Title>
                <Row>
                    <Col
                        xs={14}
                    >
                        <ul className="list">
                            <li> {hospitalData?.hospitalInfo?.hospitalName}</li>
                            <li>  {" "}
                                {(hospitalData?.hospitalInfo?.village !== "" && hospitalData?.hospitalInfo?.village !== undefined) && "" + hospitalData?.hospitalInfo?.village + ","}
                                {(hospitalData?.hospitalInfo?.commune !== "" && hospitalData?.hospitalInfo?.commune !== undefined) && "" + hospitalData?.hospitalInfo?.commune + ","}
                                {(hospitalData?.hospitalInfo?.district !== "" && hospitalData?.hospitalInfo?.district !== undefined) && "" + hospitalData?.hospitalInfo?.district + ","}

                                {hospitalData?.hospitalInfo?.province !== undefined && "" + hospitalData?.hospitalInfo?.province}</li>
                            {/* <li> {hospitalData?.hospitalInfo?.hospitalName}</li> */}
                        </ul>
                    </Col>
                    <Col
                        xs={10}
                    >
                        <ul className="list">
                            <li> {hospitalData?.date_in !== undefined && moment(hospitalData?.date_in).format("DD MM YYYY")}</li>
                            <li> {hospitalData?.date_out !== undefined && moment(hospitalData?.date_out).format("DD MM YYYY")}</li>
                        </ul>
                    </Col>
                </Row>
            </Col>
            <Col
                xs={24} md={{ span: 11 }}
                style={{ paddingTop: 14, marginTop: 20 }}
            >
                <Title level={5}> <span className="link" onClick={() => setOpenAddPeopleTest(true)} ><PlusCircleOutlined /></span></Title>
                <Table
                    columns={testCol({ handleDelete })}
                    dataSource={personalData?.sampleTest}
                    rowKey={record => record.id}
                    pagination={true}
                    scroll={{ x: 800, y: 300 }}
                    sticky
                />
            </Col>
            <Col
                xs={24} md={{ span: 12, offset: 1 }}
                style={{ paddingTop: 14, marginTop: 20 }}
            >
                <Title level={5}>
                     <span className="link" onClick={() => setOpenAddSubCase(true)}>
                        <PlusCircleOutlined />
                    </span>
                </Title>
                <Table
                    columns={relatedCol({ handleDelete })}
                    dataSource={[{ id: "1", date: "alsdjas", status: "laskldfj", remark: "laksdjald" }]}
                    rowKey={record => record.id}
                    pagination={true}
                    scroll={{ x: 1000, y: 300 }}
                    sticky
                />
            </Col>
        </Row>
    )
}
